@font-face {
  font-family: "Open Sans Condensed";
  src: url(../../static/media/OpenSansCondensed-Bold.4c789041.ttf) format("truetype");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Lora";
  src: url(../../static/media/Lora-Regular.6b74b8d6.ttf) format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Lora";
  src: url(../../static/media/Lora-Medium.2d39b3fb.ttf) format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Lora";
  src: url(../../static/media/Lora-Bold.6925b3e1.ttf) format("truetype");
  font-weight: 700;
  font-style: normal;
}
.App {
  min-height: 100vh;
}
.page {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #fff;
  padding: 0px;
  box-sizing: border-box;
}
.button {
  height: 50px;
  background-image: linear-gradient(to right, #8E4FF6, #4F92F6);
  border: none;
  outline: none;
  font-family: 'Ubuntu', sans-serif;
  font-size: 22px;
  line-height: 140%;
  font-weight: 700;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  box-shadow: 0px 4px 10px rgba(153, 162, 255, 0.6);
  border-radius: 5px;
}
.button_white {
  background: none;
  box-shadow: none;
  color: #4F92F6;
}
.button_withborder {
  background-image: none;
  background-color: white;
  border: 2px solid transparent;
  border-image: linear-gradient(89.87deg, #8E4FF6 26.68%, #4F92F6 92.81%);
  border-image-slice: 1;
  color: #101010;
  padding: 0 20px;
}
.button_disabled {
  background-image: none;
  color: #E1E1E1;
  background: #8E9295;
  cursor: no-drop;
}
.header {
  width: 100%;
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  margin: 0 auto 30px;

  font-family: 'Open Sans Condensed', sans-serif;
}
.header__logo-link {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.header__logo {
  display: block;
  height: 50px;
}
.header__logo-title {
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  margin: 0 0 0 20px;
  color: #101010;
  text-transform: uppercase;
}
.header__log-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 30px;
}
.header__login {
  font-weight: bold;
  font-size: 30px;
  line-height: 110%;
  color: #4F92F6;
}
.header__image-button {
  margin-left: 25px;
}
.header__button-container {
  display: grid;
  grid-template-columns: repeat(2, -webkit-max-content);
  grid-template-columns: repeat(2, max-content);
  grid-column-gap: 40px;
}
.header__button-sing-up {
  width: 160px;
}
.main {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  margin-bottom: 96px;
}
.footer {
  margin-top: auto;
  background: hotpink;
}
.categories {
  width: 100%;
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 60px auto 80px;
  padding: 0;
}
.categories__item {
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  font-size: 21px;
  line-height: 140%;
  text-align: center;
  color: #5D7DAD;
  cursor: pointer;
  position: relative;
}

.categories__item:before {
  content: '';
  position: absolute;
  bottom: -13px;
  left: 0px;
  width: 100%;
  height: 5px;
  background: #632EBA;
  display: block;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 0.4s cubic-bezier(1, 0, 0, 1)
}

.categories__item:hover::before {
  transform-origin: left top;
  transform: scale(1, 1)
}
.categories__item_content_all {
}
.categories__item_content_culture {
}
.categories__item_content_politics {
}
.categories__item_content_social {
}
.categories__item_content_transport {
}
.categories__item_active {
}
.petitions {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 400px));
  grid-gap: 30px;
  font-family: 'Alegreya', sans-serif;
}

@media screen and (max-width: 940px) {
  .petitions {
    grid-template-columns: repeat(2, minmax(300px, 350px));
  }
}
.petitions__item {
  background: #F4F5FF;
  min-height: 350px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0px 0px 14px #C9CEFF;
  border-radius: 10px;
  cursor: pointer;
}
.petitions__title {
  grid-column: 1/3;
  grid-row: 1/2;
  min-height: 120px;
  font-weight: 500;
  font-size: 30px;
  line-height: 120%;
  margin: 0;
}
.petitions__data {
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: end;
  align-self: flex-end;
  font-size: 14px;
  line-height: 120%;
  margin: 0;
}
.petitions__rating {
  font-size: 20px;
  line-height: 120%;
  margin: 0 0 0 5px;
}
.petitions__text {
  white-space: pre-line;
  max-height: 200px;
  overflow-y: hidden;
}
.petitions__container {
}
.petitions__container_title {
  min-height: 120px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, -webkit-max-content);
  grid-template-rows: repeat(2, max-content);
  margin: 20px 20px 15px;
}
.petitions__container_rating {
  grid-column: 1/2;
  grid-row: 2/3;
  display: grid;
  grid-template-columns: repeat(2, -webkit-max-content);
  grid-template-columns: repeat(2, max-content);
  grid-column-gap: 10px;
}
.petitions__image {
  max-width: 100%;
  height: 155px;
  margin: 0 10px 10px;
  object-fit: cover;
  object-position: center;
  border-radius: 2px;
}
.petitions__item_type_culture::after {
  background: #F66D4F;
}
.petitions__item_type_politics::after {
  background: #4F92F6;
}
.petitions__item_type_social::after {
  background: #F6DD4F;
}
.petitions__item_type_transport::after {
  background: #8E4FF6;
}
.flex {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.btn {
  background: #000;
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 0;
  color: #fff;
  position: relative;
  cursor: pointer;
  padding: 0;
  transition: opacity 0.5s ease-in-out;
}

.btn:hover {
  opacity: 0.6;
}

.btn:focus {
  outline: none;
}
.btn_like {
  background: url(../../static/media/like.cd8f5d49.svg) center no-repeat;
  background-size: cover;
}
.btn_dislike {
  background: url(../../static/media/dislike.ab04270e.svg) center no-repeat;
  background-size: cover;
}
.user {
  max-width: 1200px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
  margin: 0 auto;
}
.user__box {
  background: #DDE0FF;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 40px 0;
  padding: 70px 65px;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  box-sizing: border-box;
}
.user__image {
  object-fit: contain;
  object-position: top;
}
.user__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.user__question {
  font-family: 'Lora', sans-serif;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #8E9295;
  margin-top: 20px;
}
.user__input {
  background: #FDFDFD;
  width: 100%;
  border: none;
  font-family: 'Ubuntu', sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #8E9295;
  padding: 14px 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
  box-shadow: 0px 4px 15px rgba(153, 162, 255, 0.3);
  border-radius: 2px;
}

.user__input:hover {
  outline: 1px solid #4F92F6;;
}

.user__input:focus {
  outline: 2px solid #8E9295;
}
.user__input_password {
  margin-bottom: 0;
  width: 100%;
}
.user__pass-label {
  position: relative;
  margin-bottom: 40px;
  background: #FDFDFD;
  width: 100%;
}

.user__toggle-pass-btn {
  position: absolute;
  right: 20px;
  top: 12px;
  background: none;
  border: none;
  outline: none;
  box-shadow: none;
  width: 24px;
  height: 24px;
  padding: 0;
  margin: 0;
  background-image: url(../../static/media/input__show-pass.45cf1843.svg);
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: center;
}
.user__toggle-pass-btn_opened {
  background-image: url(../../static/media/input__hide-pass.4df3eb6c.svg);
}
.user__pass-label_focused-pass, .user__pass-label_focused-pass:hover {
  outline: 2px solid #8E9295;
}
.user__button {
  width: 255px;
}
.user__link {
  text-decoration: none;
  color: #4F92F6;
}
.add-new {
  width: 100%;
  flex: 1 1;
}
.add-new__image {
  position: relative;
  background: #F4F4F4;
  cursor: pointer;
  grid-column: 1/3;
  grid-row: 2/4;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.add-new__image:before {
  content: '';
  display: block;
  width: 60px;
  height: 60px;
  position: absolute;
  left: calc(50% - 30px);
  top: calc(50% - 30px);
  background-image: url(../../static/media/add-new__image-btn.1508f0e4.svg);
  background-repeat: no-repeat;
  background-size: cover;
  opacity: .7;
  transition: all .5s ease;
  cursor: pointer;
}

.add-new__image:hover.add-new__image::before {
  opacity: 1;
}
.add-new__image_loaded {
  
}
.add-new__image-text {
  position: absolute;
  top: calc(50% + 30px);
  left: calc(50% - 120px);
  font-family: 'Lora', sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #8E9295;
  padding: 0 20px;
  text-align: center;
}

.add-new__image_loaded .add-new__image-text {
  opacity: 0;
}
.add-new__form {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, -webkit-max-content);
  grid-template-rows: repeat(3, max-content);
  grid-gap: 30px;
  gap: 30px;
  margin: 0 0 60px;
  padding: 0;
  font-family: 'Lora', sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #8E9295;
}
.add-new__input {
  font-family: 'Lora', sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #8E9295;
  box-sizing: border-box;
  resize: none;
  padding: 20px;
  border: 2px solid #8E9295;
  grid-column: 1/3;
  grid-row: 1/2;
}

.add-new__input:hover {
  border: 2px solid #4F92F6;
  outline: none;
}
.add-new__poem-title {
  grid-column: 3/5;
  grid-row: 1/2;
  border: 2px solid #8E9295;
  margin: 0;
  padding: 20px;
  box-sizing: border-box;
}
.add-new__poem-text {
  grid-column: 3/5;
  grid-row: 2/3;
  min-height: 210px;
  border: 2px solid #8E9295;
  margin: 0;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: scroll;
  white-space: pre-line;
}

.add-new__direction {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, -webkit-max-content);
  grid-template-rows: repeat(2, max-content);
  grid-gap: 20px 120px;
  gap: 20px 120px;
  margin: 0 0 60px;
  padding: 0 0 0 20px;
}
.add-new__item {
  font-family: 'Lora', sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #101010;
}

.add-new__item:nth-child(3) {
  grid-column: 2/3;
  grid-row: 1/2;
}
.add-new__item-image {
  vertical-align: middle;
}
.add-new__title {
  font-family: 'Lora', sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 140%;
  color: #101010;
  margin: 0 0 30px;
}
.add-new__file-input {
  opacity: 0;
  cursor: pointer;
}
.opened-petition {
  position: relative;
  display: flex;
  flex-direction: column;
  background: #F4F4F4;
}

.opened-petition::after {
  content: '';
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  width: 160px;
  height: 160px;
  background: #8E9295;
  opacity: .5;
}
.opened-petition__text {
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  white-space: pre-line;
  margin: 0;
  padding: 20px 20px 50px 20px;
}
.opened-petition__image {
  position: relative;
  width: 86%;
  height: 350px;
  margin: -22px 0 0;
  background-image: url(../../static/media/opened-petition__image.bf8994d9.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  align-self: center;
}
.opened-petition__reactions {
  background: #8E9295;
  display: grid;
  grid-template-columns: repeat(2, -webkit-max-content);
  grid-template-columns: repeat(2, max-content);
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
  height: 35px;
  padding: 0 10px;
}

.opened-petition_type_culture .opened-petition__reactions {
  background:#C12B0A;
}

.opened-petition_type_politics .opened-petition__reactions {
  background:#08439B;
}

.opened-petition_type_social .opened-petition__reactions {
  background:#C1A60A;
}

.opened-petition_type_transport .opened-petition__reactions {
  background: #500AC1;
}
.opened-petition__author {
  align-self: flex-end;
  line-height: 22px;
  font-family: 'Open Sans Condensed', sans-serif;
  font-weight: bold;
  font-size: 22px;
  line-height: 140%;
  color: #101010;
}
.opened-petition__info {
  display: grid;
  grid-template-columns: 6fr 3fr;
  font-family: 'Lora', sans-serif;
}
.opened-petition_type_culture::after {
  background: #F66D4F;
}
.opened-petition_type_politics::after {
  background: #4F92F6;
}
.opened-petition_type_social::after {
  background: #F6DD4F;
}
.opened-petition_type_transport::after {
  background: #8E4FF6;
}
.opened-petition__title {
  font-family: 'Open Sans Condensed', sans-serif;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  margin: 0;
  white-space: pre-line;
}
.opened-petition__date {
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
}
.opened-petition__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 20px 50px 20px;
}

.opened-petition_type_culture .opened-petition__block {
  background: #F66D4F;
  color: black;
}

.opened-petition_type_politics .opened-petition__block {
